body {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}

.App {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.template{
    width: 600px;
    height: 500px;
    border: 1px solid;
    border-radius: 5px;
    background-color: gray;
    margin: 0 auto;
}
.template .image {
    padding: 5px;
    height: 150px;
    border: 1px solid;
    background-color: #bbbbbb;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    position: relative;
}
.template .image img{
    width: 138px;
    border-radius: 50%;
    padding: 1px;
    box-shadow: 0 0 13px 2px black;
    position: absolute;
    top: 50%;
}
.template .description{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    padding-top: 50px;
}

i{
    text-transform: capitalize;
    font-size: 35px;
    padding-top: 8px;
    cursor:pointer;
}
#element div {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    padding-top: 80px;
}
p:first-child{
    text-transform: capitalize;
}
b {
    color: #ffc107;
    padding-bottom: 10px;
}

@media(max-width:700px){
    .template{
        width: 90vw;
    }
}

